import React from 'react'
import { Link } from "react-router-dom";

function main() {
    return (
        <div className="app__container">
            <span className="app__title">제 이름은 <span className="app__name">정현수</span> 입니다.</span>
            <Link to="/2019">2019</Link>
        </div>
    )
}

export default main;
